.tool {
    &__wrapper {
        max-width: 1600px;
        margin: 0 auto;
    }

    &__title-container {
        max-width: 565 /1600 * 100%;
        padding-top: 90px;
        margin: 0 auto;
        text-align: center;
    }

    &__title {
        color: #061e37;
        font-size: 45px;
        font-weight: 600;
    }

    &__subtitle {
        margin-top: 10px;
        font-size: 25px;
        font-weight: 600;
        color: #b0bec5;
    }

    &__text {
        font-size: 15px;
        margin-top: 10px;
        line-height: 1.67;
        color: #b0bec5;
    }
}